import { createMuiTheme, ThemeProvider } from "@material-ui/core";
import Home from "./pages/Home";

const theme = createMuiTheme({
  typography: {
    palette: {
      primary: {
        main: 'white',
      },
    },
    fontFamily: [
      '"Helvetica Neue"',
    ].join(','),
  },
  spacing: 8
});

function App() {
  return (
    <ThemeProvider theme={theme}>
      <Home />
    </ThemeProvider>
  );
}

export default App;
